import React from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import path from '../helpers/path';
import { redirectExceptions } from '../helpers/redirectIfAuthorizedHelper';
import { defaultPath } from '../routeTemplates';

function redirectIfAuthorized(WrappedComponent) {
  const ModifiedComponent = props => {
    const { shouldRedirect, redirectLocation, isAuthenticated } = props;

    if (isAuthenticated) {
      if (redirectExceptions()) {
        return <WrappedComponent {...props} />;
      }
      if (shouldRedirect) {
        return (
          <Redirect
            to={{
              pathname: redirectLocation.pathname,
              search: redirectLocation.search
            }}
          />
        );
      } else {
        return <Redirect to={path(`${defaultPath.path}?default=redirect`)} />;
      }
    }

    return <WrappedComponent {...props} />;
  };

  const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    shouldRedirect: state.auth.shouldRedirect,
    redirectLocation: state.auth.location
  });

  return connect(
    mapStateToProps,
    null
  )(ModifiedComponent);
}

export default redirectIfAuthorized;



