import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import APInput from '../common/APInput/APInput';
import APButton from '../common/APButton';
import APIcon from '../common/APIcon/APIcon';
import { required } from '../../helpers/formValidations';
import APPasswordInput from '../common/APPasswordInput/APPasswordInput';
import redirectIfAuthorized from '../../hocs/redirectIfAuthorized';
import path from '../../helpers/path';
import config from '../../config';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import TradingServerComponent from '../TradingServer/TradingServerComponent';
import { styleNames } from '../../propTypes/commonComponent';

import '../../styles/components/common/StandaloneForm.css';
import './LoginFormComponent.css';

const baseClasses = getBEMClasses('standalone-form');
const loginFormClasses = getBEMClasses('login-form');

export const LoginFormComponent = (props, context) => {
  const { handleSubmit, errorMsg, submitting, doSubmit, isConnected } = props;
  return (
    <div
      className={`${baseClasses('container')} ${loginFormClasses(
        'container'
      )}`}>
      <div className={`${baseClasses('header')} ${loginFormClasses('header')}`}>
        <div
          className={`${baseClasses('header-text')} ${loginFormClasses(
            'header-text'
          )}`}>
          {context.t('Log In to {n}', { n: config.global.siteName })}
        </div>
        <Link to={path('/')} className={baseClasses('close-icon')}>
          <APIcon name="close" classModifiers="big" />
        </Link>
      </div>
      <form
        onSubmit={handleSubmit(values => doSubmit(values, isConnected))}
        className={`${baseClasses('form')} ${loginFormClasses('form')}`}>
        {errorMsg && <p className={baseClasses('error')}>{errorMsg}</p>}
        <APInput
          type="text"
          name="username"
          customClass={loginFormClasses()}
          label={context.t('Username')}
          validate={[required]}
        />
        <APPasswordInput
          type="password"
          name="password"
          customClass={loginFormClasses()}
          label={context.t('Password')}
          validate={[required]}
        />
        {Array.isArray(config.global.gateway) && <TradingServerComponent />}
        <hr className={loginFormClasses('separator')} />
        <APButton
          type="submit"
          disabled={submitting}
          customClass={loginFormClasses()}
          styleName={styleNames.additive}>
          {submitting ? context.t('Processing...') : context.t('Log In')}
        </APButton>
      </form>

      <div className={`${baseClasses('footer')} ${loginFormClasses('footer')}`}>
        <Link
          to={path('/signup')}
          className={loginFormClasses('link')}
          disabled>
          {context.t('Sign Up')}
        </Link>
        <div className={baseClasses('footer-separator')} />
        <Link
          to={path('/problem-logging-in')}
          className={loginFormClasses('link')}
          disabled>
          {context.t('Problem Logging In?')}
        </Link>
      </div>
    </div>
  );
};

LoginFormComponent.defaultProps = {
  handleSubmit: () => {},
  submitting: false
};

LoginFormComponent.propTypes = {
  handleSubmit: PropTypes.func,
  submitting: PropTypes.bool,
  referrer: PropTypes.string
};

LoginFormComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default redirectIfAuthorized(LoginFormComponent);



