import React from 'react';
import PropTypes from 'prop-types';
import APSelect from '../common/APSelect/APSelect';
import config from '../../config';
import { getGateway } from '../../helpers/wsHelper';
import { changeServer } from '../../redux/actions/wsConnectionActions';
import { getBEMClasses } from '../../helpers/cssClassesHelper';

import './TradingServerComponent.css';

const baseClasses = getBEMClasses('trading-server');

class TradingServerComponent extends React.Component {
  render() {
    const currentServer = getGateway();
    const tradingServers = config.global.gateway
      .filter(item => item !== currentServer)
      .map(item => ({ value: item, label: item }));

    return (
      <APSelect
        name="tradingServer"
        label={`${this.context.t('Select server')}:`}
        onSelect={value => changeServer(value)}
        options={[
          { value: currentServer, label: currentServer },
          ...tradingServers
        ]}
        customClass={baseClasses()}
      />
    );
  }
}

TradingServerComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default TradingServerComponent;



